body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App a {
  color: black;
}

h3 {
  text-align: center;
}
.bg-orange {
  background: rgb(255, 102, 0);
  box-shadow: 0px 5px 10px grey;
  margin-bottom: 5px;
}

.App .no-left-padding, .App .list-group-flush {
  padding-left: 0px;
}

nav a img {
  padding: 5px;
  background: white;
}
nav.container, .app-body.container {
  padding: 0px;
}
nav li.active {
  background: rgb(255, 69, 0);
  text-decoration: underline;
}
nav li:hover {
  background: rgb(255, 69, 0);
}
nav li{
  margin-right: 15px;
}
nav li a {
  color: white !important;
  padding: 1.2rem 1.5rem !important;
}

.app-body ul {
  padding-bottom: 10px;
}

li h2 {
  font-size: 18px;
}
li h4 {
  font-size: 14px;
}

.simple-item p {
  font-size: 10px;
  margin-bottom: 5px;
  color: grey;
}

.main-item p  {
  font-size: 11px;
  margin-bottom: 5px;
  color: grey;
}

.item-footer a {
  color: blue !important;
}

ul.comment-tree li {
  list-style: none;
}

.comment-tree .list-group-item {
  border-left: none;
}

.comment-tree .comment {
  padding-left: 20px;
  border-left: 2px dotted darkgrey;
}
.comment-tree .comment:hover {
  padding-left: 20px;
  border-left: 2px solid rgb(255, 69, 0);
}
.comment-tree .list-group, .comment-tree .list-group-item {
  padding-bottom: 0px;
}

.comment-tree>li {
  margin-bottom: 20px;
  border-radius: 10px;
}

.comment>p {
  font-size: 12px;
}
.comment>p.comment-header, .comment>p.comment-footer {
  font-size: 10px;
  color: grey;
}

div.spinner {
  border: 3px solid lightgrey;
  border-top: 3px solid rgb(255, 102, 0);
  border-bottom: 3px solid rgb(255, 102, 0);
  border-radius: 50%;
  width: 35px;
  height: 35px;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  margin: auto;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
